import './App.scss'
import TheHeader from './Layout/TheHeader.jsx'
import { Routes, Route } from "react-router-dom"
import TheContent from './Layout/TheContent.jsx'

import Home from './pages/Home.jsx'
import About from './pages/About'
import System from './pages/System.jsx'
import Login from './pages/Login.jsx'
import LabSystem from './pages/LabSystem'
import Unauthorized from './pages/Unauthorized'
// import Require from './components/Require'

function App() {
    return (
        <div>
            <TheHeader></TheHeader>
            <TheContent>
                <Routes>
                    <Route path="/" element={<Home />} />
                    <Route path="/about" element={<About />} />

                    {/* <Route element={<Require level="2" />}> */}
                        <Route path="system" element={<System />} />
                    {/* </Route> */}
                    {/* <Route element={<Require level="1" />}> */}
                        <Route path="labsystem" element={<LabSystem />}/>
                    {/* </Route> */}

                    <Route path="login" element={<Login />} />
                    <Route path="unauthorized" element={<Unauthorized />} level="1" />
                </Routes>
            </TheContent>
        </div>
    )
}

export default App
