import React from 'react'
import axios from 'axios'
import { useForm } from 'react-hook-form'
import { useNavigate } from 'react-router-dom'
import Cookies from 'js-cookie'

import useAuth from '../hook/useAuth'

function Login() {
    const { setAuth } = useAuth() 
    const navigate = useNavigate()
    const [feedback, setFeedback] = React.useState(null)
    const { register, handleSubmit } = useForm({mode: 'onChange'})

    const onSubmit = async (input) => {
        setFeedback(null)
        const response = await axios.post('api/login', {
            email: input.email,
            password: input.password,
        })
        if (response.data.status) {
            setAuth({user: response.data.email, level: response.data.level})
            Cookies.set('user', JSON.stringify(response.data))
            navigate('/system', { replace: true })
        } else  {
            setFeedback({wrong: '你的帳號或密碼不正確，請再試一次'})
        }
    }

    const onError = (error, e) => {
        setFeedback(error)
    }

    return (
        <div className="login">
            <div className="container-xl">
                <form onSubmit={handleSubmit(onSubmit, onError)} className="rounded-3">
                    <div className="login-title">
                        <h1 className="fs-3">登入</h1>
                    </div>
                    <div class="alert alert-warning alert-dismissible fade show" role="alert">
                         Acocunt: x@x.com
                         <p>admin password: 1NHJJ</p>  user password: 2
                        <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                    </div>
                    <div className="field-input">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" {...register('email',
                            { 
                                required: "請輸入電子郵件地址",
                                pattern: {
                                    value: /[a-zA-z0-9]@[a-z]+.com/,
                                    message: '格式不符合',
                                }
                         })} type="text" id="email" placeholder="電子郵件地址" />
                    </div>
                    <div className="back">
                        <p className="input-feedback">{feedback?.email?.message}</p>
                    </div>
                    <div className="field-input">
                        <label htmlFor="password">Password</label>
                        <input className="form-control" {...register('password', {
                            required: '請輸入密碼'
                        })} type="text" id="password" placeholder="輸入您的密碼"/>
                    </div>
                    <div className="back">
                        <p className="input-feedback">{feedback?.password?.message}</p>
                    </div>
                    <button className="btn btn-primary login-button">登入</button>
                    <div className="back">
                        <p className="input-feedback">{feedback?.wrong}</p>
                    </div>
                </form>
            </div>
        </div>
    )
}

export default Login