import React from 'react'

function SystemInfo({register}) {
    return (
        <>
            <h4 className="fs-4">系統資源</h4>
            {/* box */}
            {/* 1 */}
            <div className="box">
                <h5 className="fs-5">GPU cards per container</h5>
                <p className="fs-6 mb-3">借用2顆以上需評估，如有需求請聯絡管理團隊。</p>
                <select className="form-select" { ...register('gpu')}>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                </select>
            </div>

            <div className="box-seperate"></div>

            {/* 2 */}
            <div className="box">
                <h5 className="fs-5">CPU threads per container</h5>
                <p className="fs-6 mb-3">input 1~12, eg: 4</p>
                <div className="input-field">
                    <input className="box-input" placeholder="1" type="text" {...register('cpu', {
                        required: '請輸入CPU threads',
                        min: 1,
                        max: 12,
                    })} />
                </div>
            </div>

            <div className="box-seperate"></div>

            {/* 3 */}
            <div className="box">
                <h5 className="fs-5">System memory per container</h5>
                <p className="fs-6 mb-3">一顆GPU 原則上分配 8GB 記憶體，申請上限為80GB。</p>
                <div className="input-field">
                    <input className="box-input" placeholder="1" type="text" {...register('memory', {
                        required: '請輸入記憶體配制量',
                        min: 1,
                        max: 8,
                    })}/>
                </div>
            </div>
            <div className="box-seperate"></div>
            {/* 4 */}
            <div className="box">
                <h5 className="fs-5">Image Name</h5>
                <p className="fs-6 mb-3">一顆GPU 原則上分配 8GB 記憶體，申請上限為80GB。</p>
                <select className="form-select" {...register('imagename')}>
                    <option value="webtop_matlab">webtop_matlab</option>
                    <option value="webtop_orange3_CLC">webtop_orange3_CLC</option>
                    <option value="webtop_itksnap">webtop_itksnap</option>
                    <option value="webtop_itksnap_mitkworkbench_3dslicer">webtop_itksnap_mitkworkbench_3dslicer</option>
                </select>
            </div>
        </>
    )
}

export default SystemInfo