import React from 'react'

function Connect({register}) {
    return (
        <>
            <h4 className="fs-4">連線方式</h4>

            {/* box */}
            {/* 1 */}
            <div className="box">
                <div className="input-field">
                    <input type="checkbox" id="ftp" {...register('connect')} value="ftp"/>
                    <label htmlFor="ftp">FTP</label>
                </div>
             
                <div className="input-field">
                    <input type="checkbox" id="webtop" {...register('connect')} value="webtop"/>
                    <label htmlFor="webtop">Webtop</label>
                </div>
                <div className="input-field">
                    <input type="checkbox" id="webide" {...register('connect')} value="webide"/>
                    <label htmlFor="webide">WebIDE</label>
                </div>
                <div className="input-field">
                    <input type="checkbox" id="ssh" {...register('connect')} value="ssh"/>
                    <label htmlFor="ssh">SSH</label>
                </div>
            </div>
        </>
    )
}

export default Connect