import React, { useEffect } from 'react'
import useUser from '../hook/useUser'
import { Chart, registerables } from 'chart.js';



function LabSystem() {
    const isUser = useUser()
    const { username, user } = isUser ? JSON.parse(isUser) : null
    const data = [
        {
            task: '聲學分析',
            description: '台大oo老師聲學案',
            imagename: 'Matlab_v1',
            starttime: '2022/03/01',
            endtime: '2022/03/02',
            amounttime: '36hr',
            expectendtime: '已結案',
            ftp: 'vjfjios',
            webtop: '333',
            webide: 'wdwd',
            ssh: 'dfsd',
            status: 'close',
        },
        {
            task: 'Lung Cancer',
            description: '台大oo老師聲學案',
            imagename: 'Matlab_v1',
            starttime: '2022/03/01',
            endtime: '2022/03/02',
            amounttime: '36hr',
            expectendtime: '已結案',
            ftp: 'vjfjios',
            webtop: '333',
            webide: 'wdwd',
            ssh: 'dfsd',
            status: '執行中',
        },
        {
            task: 'Colon Caner',
            description: '國衛院老師聲學案',
            imagename: 'CLCv3',
            starttime: '2022/05/01',
            endtime: 'ongoing',
            amounttime: '855hr',
            expectendtime: '2022/10/31',
            ftp: 'vj_d72s',
            webtop: 'URL:2684',
            webide: 'afd3da',
            ssh: 'fdtovjd7',
            status: '執行中',
        },
    ]
    const labels = [
        'January',
        'February',
        'March',
        'April',
        'May',
        'June',
      ];
    
      const piedata = {
        labels: labels,
        datasets: [{
          label: 'My First dataset',
          backgroundColor: 'rgb(255, 99, 132)',
          borderColor: 'rgb(255, 99, 132)',
          data: [0, 10, 5, 2, 20, 30, 45],
        }]
      };
    
      const config = {
        type: 'line',
        data: piedata,
        options: {}
      };
    Chart.register(...registerables)
    
    useEffect(() => {
        new Chart(
            document.getElementById('myChart'),
            config
        );
    }, [])
    return (
        <div className="container-xl">
            <h1>LabSystem</h1>
            <p className="user">{username}{user}</p>
            <div className="table-responsive">
                <table className="table table-striped table-third">
                    <thead>
                        <tr>
                            <th>Task</th>
                            <th>描述</th>
                            <th>Image Name</th>
                            <th>開始時間</th>
                            <th>給束時間</th>
                            <th>累計時長</th>
                            <th>預計結束時間</th>
                            <th>ftp</th>
                            <th>GUI(webtop)</th>
                            <th>GUI(web IDE)</th>
                            <th>SSH</th>
                            <th>Status</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map((item, index) => {
                                return (
                                    <tr key={index}>
                                        <td>{item.task}</td>
                                        <td>{item.description}</td>
                                        <td>{item.imagename}</td>
                                        <td>{item.starttime}</td>
                                        <td>{item.endtime}</td>
                                        <td>{item.amounttime}</td>
                                        <td>{item.expectendtime}</td>
                                        <td>{item.ftp}</td>
                                        <td>{item.webtop}</td>
                                        <td>{item.webide}</td>
                                        <td>{item.ssh}</td>
                                        <td>{item.status}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
            <div>
                <canvas id="myChart"></canvas>
            </div>
        </div>
    )
}

export default LabSystem